/* src/NavBar.css */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 20px;
  background-color: #00bcd4;
  color: white;
  flex-wrap: wrap;
}

.navbar-logo {
  font-weight: bold;
  font-size: 1.5em;
}

.navbar-links .nav-btn {
  background: none;
  border: none;
  color: white;
  margin-left: 12px;
  cursor: pointer;
  font-weight: bold;
  padding: 6px 10px;
  border-radius: 5px;
  transition: 0.2s;
}

.navbar-links .nav-btn:hover,
.navbar-links .nav-btn.active {
  background-color: rgba(255,255,255,0.3);
}
