/* src/App.css */
.app {
  font-family: 'Arial', sans-serif;
  padding: 0;
  margin: 0;
}

.page-content {
  padding: 20px;
}

.search-bar {
  width: 100%;
  max-width: 400px;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.animal-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 16px;
}

.region-picker {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
}

.region-btn {
  padding: 8px 12px;
  border-radius: 8px;
  border: none;
  background-color: #f0f0f0;
  cursor: pointer;
  transition: all 0.2s;
}

.region-btn.active,
.region-btn:hover {
  background-color: #00bcd4;
  color: white;
}

h1, h2 {
  text-align: center;
}

@media (max-width: 600px) {
  .animal-grid {
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  }

  .search-bar {
    max-width: 100%;
  }
}
