/* src/AnimalCard.css */
.animal-card {
  background-color: #f9f9f9;
  border-radius: 12px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  text-align: center;
  padding-bottom: 10px;
}

.animal-card:hover {
  transform: scale(1.05);
  box-shadow: 0px 8px 15px rgba(0,0,0,0.2);
}

.animal-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-bottom: 1px solid #ddd;
  border-radius: 12px 12px 0 0;
}
